import React, { forwardRef } from 'react';
import { IconButton } from '@element/react-icon-button';
import { Icon } from '@element/react-icon';
import clsx from 'clsx';
import {
    ActionItemPropTypes,
    ActionItemDefaultProps
} from './generated/propTypes';

const ActionItem = forwardRef((props, externalRef) => {
    const { badge, type, label, icon, className, iconType, ...customProps } =
        props;
    if (type === 'link') {
        return (
            <a
                ref={externalRef}
                {...customProps}
                className='lmnt-top-app-bar__action-item--link mdc-typography--subtitle2'
            >
                {label}
            </a>
        );
    }
    return (
        <IconButton
            ref={externalRef}
            aria-label={label}
            className={clsx('lmnt', 'mdc-top-app-bar__action-item', className)}
            {...customProps}
        >
            <Icon icon={icon} badge={badge} badgePlacement='left-start' />
        </IconButton>
    );
});

ActionItem.displayName = 'TopAppBarActionItem';
ActionItem.propTypes = ActionItemPropTypes;
ActionItem.defaultProps = ActionItemDefaultProps;

export default ActionItem;
