import React, { forwardRef, useImperativeHandle, useRef } from 'react';
import clsx from 'clsx';
import {
    TopAppBarContentWrapperPropTypes,
    TopAppBarContentWrapperDefaultProps
} from './generated/propTypes';

const TopAppBarContentWrapper = forwardRef((props, externalRef) => {
    const { children, className, ...customProps } = props;
    const ref = useRef();
    useImperativeHandle(
        externalRef,
        () => ({
            current: ref.current
        }),
        []
    );
    return (
        <div
            ref={ref}
            className={clsx('lmnt', 'lmnt-top-app-bar__content', className)}
            {...customProps}
        >
            {children}
        </div>
    );
});

TopAppBarContentWrapper.displayName = 'TopAppBarTopAppBarContentWrapper';
TopAppBarContentWrapper.propTypes = TopAppBarContentWrapperPropTypes;
TopAppBarContentWrapper.defaultProps = TopAppBarContentWrapperDefaultProps;

export default TopAppBarContentWrapper;
