import clsx from 'clsx';
import React, { forwardRef } from 'react';
import {
    TopAppBarFixedAdjustPropTypes,
    TopAppBarFixedAdjustDefaultProps
} from './generated/propTypes';

const TopAppBarFixedAdjust = forwardRef((props, externalRef) => {
    const {
        bannerOpen,
        children,
        className,
        dense,
        prominent,
        tag: Tag,
        useContentMaxWidth,
        ...customProps
    } = props;

    return (
        <Tag
            ref={externalRef}
            className={clsx(
                bannerOpen && 'lmnt-top-app-bar-open-banner--fixed-adjust',
                !prominent && !dense && 'mdc-top-app-bar--fixed-adjust',
                prominent &&
                    !dense &&
                    'mdc-top-app-bar--prominent-fixed-adjust',
                !prominent && dense && 'mdc-top-app-bar--dense-fixed-adjust',
                prominent &&
                    dense &&
                    'mdc-top-app-bar--dense-prominent-fixed-adjust',
                useContentMaxWidth &&
                    'lmnt-top-app-bar-fixed-adjust-with-max-content-width',
                className
            )}
            {...customProps}
        >
            {children}
        </Tag>
    );
});

TopAppBarFixedAdjust.displayName = 'TopAppBarFixedAdjust';
TopAppBarFixedAdjust.propTypes = TopAppBarFixedAdjustPropTypes;
TopAppBarFixedAdjust.defaultProps = TopAppBarFixedAdjustDefaultProps;

export default TopAppBarFixedAdjust;
