import React, {
    useRef,
    useEffect,
    useMemo,
    forwardRef,
    useImperativeHandle,
    useState
} from 'react';
import clsx from 'clsx';
import {
    addProps,
    edsQuerySelector,
    ConditionalWrapper
} from '@element/react-common';
import { MDCTopAppBar } from '@material/top-app-bar';
import TopAppBarContentWrapper from './TopAppBarContentWrapper';
import {
    TopAppBarDefaultProps,
    TopAppBarPropTypes
} from './generated/propTypes';

const createWrappedLogoTitle = Wrapper => children =>
    (
        <Wrapper>
            <div className='lmnt-top-app-bar__logo-title-wrapper'>
                {children}
            </div>
        </Wrapper>
    );

const createWrappedContent = Wrapper => children =>
    <Wrapper>{children}</Wrapper>;

const TopAppBar = forwardRef((props, externalRef) => {
    const {
        actions,
        children,
        className,
        contextual,
        contextualActions,
        contextualTheme,
        dense,
        elevated,
        fixed,
        logo,
        logoSize,
        logoTitleWrapper,
        navigation,
        onNavigation,
        prominent,
        prominentTabs,
        scrollTarget,
        subtitle,
        tabActions,
        themeColor,
        title,
        useContentMaxWidth,
        ...customProps
    } = props;

    const ref = useRef();
    const mdcTopAppBarRef = useRef();
    const actionsActual = contextual ? contextualActions : actions;

    const [height, setHeight] = useState();

    useImperativeHandle(
        externalRef,
        () => ({
            height,
            current: ref.current
        }),
        [height]
    );

    const isTabs = useMemo(
        () => actionsActual?.type?.displayName === 'TabBar' || tabActions,
        [actionsActual, tabActions]
    );

    const isProminent = useMemo(
        () => prominent || prominentTabs,
        [prominent, prominentTabs]
    );

    const showActions = useMemo(() => {
        if (isTabs && prominentTabs) return false;
        return !!actionsActual;
    }, [actionsActual, isTabs, prominentTabs]);

    useEffect(() => {
        mdcTopAppBarRef.current = MDCTopAppBar.attachTo(ref.current);
        setHeight(
            mdcTopAppBarRef.current.foundation.adapter.getTopAppBarHeight()
        );
        return () => {
            mdcTopAppBarRef.current.destroy();
        };
    }, []);

    useEffect(() => {
        if (scrollTarget !== 'window') {
            if (typeof scrollTarget === 'string') {
                const target = edsQuerySelector(scrollTarget);
                if (target) mdcTopAppBarRef.current.setScrollTarget(target);
            } else {
                mdcTopAppBarRef.current.setScrollTarget(scrollTarget);
            }
        }
    }, [scrollTarget]);

    const titleAndActions = useMemo(
        () => (
            <>
                <section className='mdc-top-app-bar__section mdc-top-app-bar__section--align-start'>
                    {navigation &&
                        addProps(navigation, {
                            onClick: onNavigation,
                            className: 'mdc-top-app-bar__navigation-icon'
                        })}
                    <ConditionalWrapper
                        condition={logoTitleWrapper}
                        wrapper={createWrappedLogoTitle(logoTitleWrapper)}
                    >
                        {logo && (
                            <div
                                className={clsx(
                                    'lmnt-top-app-bar__logo',
                                    logoSize === 'small' &&
                                        'lmnt-top-app-bar__logo--small',
                                    logoSize === 'medium' &&
                                        'lmnt-top-app-bar__logo--medium',
                                    logoSize === 'large' &&
                                        'lmnt-top-app-bar__logo--large',
                                    logoSize === 'lgSquare' &&
                                        'lmnt-top-app-bar__logo--large-square'
                                )}
                            >
                                {logo}
                            </div>
                        )}
                        {(title || subtitle) && !children && (
                            <div
                                className={clsx(
                                    'mdc-top-app-bar__title',
                                    subtitle &&
                                        'mdc-top-app-bar__title--with-subtitle'
                                )}
                            >
                                {!children && title && <span>{title}</span>}
                                {!children && subtitle && (
                                    <span className='lmnt-top-app-bar__subtitle mdc-typography--subtitle2'>
                                        {subtitle}
                                    </span>
                                )}
                            </div>
                        )}
                    </ConditionalWrapper>
                </section>

                {!title && !subtitle && children && (
                    <section
                        className='mdc-top-app-bar__section lmnt-top-app-bar__children'
                        role='toolbar'
                    >
                        {children}
                    </section>
                )}
                {showActions && (
                    <section
                        className={clsx(
                            'mdc-top-app-bar__section',
                            'mdc-top-app-bar__section--align-end',
                            isTabs && 'lmnt-top-app-bar__section--tab-actions'
                        )}
                        role='toolbar'
                    >
                        {actionsActual}
                    </section>
                )}
            </>
        ),
        [
            actionsActual,
            children,
            isTabs,
            logo,
            logoSize,
            logoTitleWrapper,
            navigation,
            onNavigation,
            showActions,
            subtitle,
            title
        ]
    );

    const themeColorClass = useMemo(() => {
        if (themeColor.includes('gradient'))
            return `lmnt-theme--bg-${themeColor}`;
        return `lmnt-top-app-bar--${themeColor}`;
    }, [themeColor]);

    return (
        <header
            ref={ref}
            {...customProps}
            className={clsx(
                'lmnt',
                'mdc-top-app-bar',
                'lmnt-top-app-bar',
                isProminent && 'mdc-top-app-bar--prominent',
                prominentTabs && 'mdc-top-app-bar--prominent-tabs',
                dense && 'mdc-top-app-bar--dense',
                fixed && 'mdc-top-app-bar--fixed',
                themeColorClass,
                elevated && 'mdc-elevation--z4',
                contextual && 'lmnt-top-app-bar--contextual',
                contextualTheme === 'dark' &&
                    'lmnt-top-app-bar--contextual-dark',
                contextualTheme === 'light' &&
                    'lmnt-top-app-bar--contextual-light',
                useContentMaxWidth && 'lmnt-top-app-bar-with-max-content-width',
                className
            )}
        >
            <ConditionalWrapper
                condition={useContentMaxWidth}
                wrapper={createWrappedContent(TopAppBarContentWrapper)}
            >
                <div className='mdc-top-app-bar__row'>
                    {prominentTabs ? (
                        <div className='lmnt-top-app-bar__title-and-actions'>
                            {titleAndActions}
                        </div>
                    ) : (
                        titleAndActions
                    )}
                    {prominentTabs && (
                        <div className='lmnt-top-app-bar__prominent-tabs'>
                            {prominentTabs}
                        </div>
                    )}
                </div>
            </ConditionalWrapper>
        </header>
    );
});

TopAppBar.displayName = 'TopAppBar';
TopAppBar.propTypes = TopAppBarPropTypes;
TopAppBar.defaultProps = TopAppBarDefaultProps;

export default TopAppBar;
